import { connect } from 'react-redux';
import { editSupportInfo } from '../../../actions/actionCreator';
import React, { Component } from 'react'
import { Table, Button, Checkbox, Form, Row, Col, Icon } from 'antd'
import BasicInfo from './basicInfo';

class SupportInfo extends Component {
    state = {

    }
    componentWillMount() {

    }

    handleEdit = (e) => {
        this.props.dispatch(editSupportInfo());
    }

    render() {
        let { supportInfo } = this.props.buildInfo;
        return (
            <div style={{ marginTop: '25px', backgroundColor: "#ECECEC" }}>
                <Form layout="horizontal" >
                    <Row type="flex" style={{ padding: '1rem 0' }}>
                        <Col span={20}>
                            <Icon type="tags-o" className='content-icon' /><span className='content-title'></span>
                        </Col>
                        <Col span={4}>
                            {
                                [1, 8].includes(this.props.buildInfo.examineStatus) ? null : <Button type="primary" shape="circle" icon="edit" onClick={this.handleEdit} />
                            }
                        </Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={4}></Col>
                        <Col span={20}>
                            <Row>
                                <Col>{supportInfo.busDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.railDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.otherTrafficDesc || ""}</Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={4}></Col>
                        <Col span={20}>
                            <Row>
                                <Col span={4}>{supportInfo.kindergartenDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.primarySchoolDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.middleSchoolDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.universityDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.marketDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.supermarketDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.hospitalDesc || ""}</Col>
                            </Row>
                            <Row>
                                <Col span={4}>{supportInfo.bankDesc || ""}</Col>
                            </Row>
                        </Col>
                    </Row>
                </Form>
            </div>
        )
    }
}

function mapStateToProps(state) {
    //console.log('BuildingDishDetail MapStateToProps:' + JSON.stringify(state));
    return {
        buildInfo: state.building.buildInfo,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch
    };
}
export default connect(mapStateToProps, mapDispatchToProps)(SupportInfo);


